html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}
body {
  margin: 0;
}
a {
  text-decoration: none;
  margin: 0;
  padding: 0;
}
ul {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  width: 100%;
  height: 100%;
}

.section {
  padding-top: 20px;
  padding-bottom: 40px;

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.main-container {
  width: 320px;
  padding: 10px;
  margin-right: auto;
  margin-left: auto;
  align-content: center;
}
@media screen and (min-width: 768px) {
  .main-container {
    width: 768px;
    padding: 40px 60px;
  }
}
@media screen and (min-width: 1024px) {
  .main-container {
    width: 1024px;
    padding-left: 56px;
    padding-right: 56px;
  }
}

.gallery {
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: -15px;
  }
}
.gallery-item-card {
  display: flex;
  flex-direction: column;
  justify-content: center;

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 20px;
    }
  }

  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 2 * 30px) / 2);
    margin: 15px;
  }

  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 3 * 30px) / 3);
    margin: 15px;
  }
}

.card {
  cursor: pointer;
}

.poster-container {
  width: 280px;
  height: 398px;
  margin-bottom: 10px;
  border-radius: 5px;
  object-fit: contain;
  overflow: hidden;

  @media screen and (min-width: 768px) {
    width: 294px;
  }

  @media screen and (min-width: 1024px) {
    width: 274px;
  }
}

.text-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  text-transform: uppercase;
  color: #000000;
}

.text-ganre {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #ff6b08;
}

.text-rating {
  width: 36px;
  height: 16px;
  margin-left: 10px;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  color: #ffffff;
  background-color: #ff6b01;
}
.text-details {
  display: flex;
  align-items: baseline;
  margin-top: 7px;
}
.is-hidden {
  display: none;
}
